// components/ChatBot.js
import { useState } from 'react';
import styles from '../styles/ChatBot.module.css';
import axios from 'axios';

const ChatBot = () => {
  const [messages, setMessages] = useState([]);
  const [userInput, setUserInput] = useState('');
  const [loading, setLoading] = useState(false);

  const sendMessage = async () => {
    if (!userInput.trim()) return;

    const newMessages = [...messages, { role: 'user', content: userInput }];
    setMessages(newMessages);
    setLoading(true);

    try {
      const res = await axios.post('/api/chat', { userInput });
      const aiMessage = res.data.response;

      setMessages([...newMessages, { role: 'assistant', content: aiMessage }]);
      setUserInput('');
    } catch (err) {
      console.error(err);
    }

    setLoading(false);
  };

  return (
    <div className={styles.chatContainer}>
      <h2>ðŸ’¬ Credit Intelligence Chatbot</h2>
      <div className={styles.chatBox}>
        {messages.map((msg, idx) => (
          <div key={idx} className={msg.role === 'user' ? styles.user : styles.ai}>
            {msg.content}
          </div>
        ))}
        {loading && <div className={styles.ai}>Typing...</div>}
      </div>
      <div className={styles.inputSection}>
        <input
          type="text"
          value={userInput}
          placeholder="Ask me about credit scores..."
          onChange={(e) => setUserInput(e.target.value)}
        />
        <button onClick={sendMessage}>Send</button>
      </div>
    </div>
  );
};

export default ChatBot;
